html, body {
  width: 100vw;
  height: 100vh;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
}

body {
  perspective: 1000px;
  background: url("baba_s_frazoi.79fd958e.jpg") center / cover no-repeat;
  margin: 0;
  font-family: Caveat, cursive;
  overflow: hidden;
}

.greet {
  background: url("we.8347828f.jpg") center / cover no-repeat;
}

.dot {
  width: 35px;
  height: 35px;
  background: url("petal.9a23bacd.png") 0 0 / 100% 100%;
  position: absolute;
}

#petals {
  width: 100%;
  height: 100%;
}

.card {
  width: 90vw;
  z-index: 1;
  opacity: 0;
  background-color: #fff;
  padding: 15px 30px;
  animation: card 2s linear 2s forwards;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 0 20px rgba(0, 0, 0, .1);
}

.title {
  background-image: linear-gradient(110deg, #ff6945 0%, #8047ca 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #ff6945;
  text-align: center;
  margin-bottom: 10px;
  font-size: 70px;
  font-weight: 700;
  line-height: 1;
}

.text {
  color: #333;
  margin-bottom: 20px;
  font-size: 30px;
  font-weight: 700;
  line-height: 1.2;
}

.author {
  background-image: linear-gradient(110deg, #ff6945 0%, #8047ca 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: 0;
  color: #121212;
  text-align: right;
  padding-right: 40px;
  font-size: 40px;
  transition: opacity 1s linear;
}

@keyframes card {
  from {
    opacity: 0;
  }

  to {
    opacity: .6;
  }
}

.sun {
  width: 150px;
  height: 150px;
  opacity: 0;
  animation: sun 4s linear forwards;
  display: none;
  position: absolute;
  top: -100px;
  left: 200px;
}

@keyframes sun {
  from {
    opacity: 0;
    top: -100px;
  }

  to {
    opacity: 1;
    top: 60px;
  }
}

.face {
  width: 80px;
  height: 87px;
  opacity: 0;
  animation: face 2s linear forwards;
  display: none;
  position: absolute;
  top: 42%;
  left: 46%;
}

@keyframes face {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.me {
  animation: me 3s linear 1s forwards;
  display: none;
  position: absolute;
  top: 130%;
  left: 40%;
}

@keyframes me {
  from {
    top: 130%;
  }

  to {
    top: 80%;
  }
}

.heart {
  width: 60px;
  opacity: 0;
  animation: heart 1s linear 4s forwards;
  display: none;
  position: absolute;
  top: 68%;
  left: 80%;
}

@keyframes heart {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.btn {
  cursor: pointer;
  position: absolute;
  top: 30%;
  left: 30%;
}

.is-hidden {
  display: block;
}

.link {
  cursor: pointer;
  font-size: 50px;
  text-decoration: none;
  position: absolute;
  top: 90%;
  left: 5%;
}

.cta {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: auto;
  padding: 19px 22px;
  transition: all .2s;
  position: relative;
  top: 30px;
  left: 40%;
}

.cta:before {
  content: "";
  width: 56px;
  height: 56px;
  background: #ffab9d;
  border-radius: 28px;
  transition: all .3s;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.cta span {
  letter-spacing: .25em;
  text-transform: uppercase;
  vertical-align: middle;
  font-size: 16px;
  font-weight: 900;
  line-height: 18px;
  position: relative;
}

.cta svg {
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke: #111;
  stroke-width: 2px;
  margin-left: 10px;
  transition: all .3s;
  position: relative;
  top: 0;
  transform: translateX(-5px);
}

.cta:hover.cta:before {
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
}

.cta:hover.cta svg {
  display: none;
}

.cta:hover.cta span {
  color: rgba(0, 0, 0, 0);
}

