html,
body {
  width: 100vw;
  height: 100vh;
}
img {
  max-width: 100%;
  height: auto;
  margin: 0;
}
body {
  margin: 0;
  overflow: hidden;
  perspective: 1000px;
  font-family: 'Caveat', cursive;
  background: url('img/baba_s_frazoi.jpg') no-repeat;
  background-size: cover;
  background-position: center;
}
.greet {
  background: url('img/we.jpg') no-repeat;
  background-size: cover;
  background-position: center;
}
.dot {
  width: 35px;
  height: 35px;
  position: absolute;
  background: url('img/petal.png');
  background-size: 100% 100%;
}
#petals {
  width: 100%;
  height: 100%;
}
.card {
  width: 90vw;
  /* max-width: 800px; */
  /* min-height: 420px; */
  padding: 15px 30px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  /* transform-style: preserve-3d; */
  /* perspective: 200px; */
  opacity: 0;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  /* transform: translate3d(-40%, -50%, 0) rotateY(15deg); */
  animation: card 2s linear 2s forwards;
}

.title {
  margin-bottom: 10px;
  background-image: linear-gradient(110deg, #ff6945 0%, #8047ca 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #ff6945;
  font-size: 70px;
  font-weight: 700;
  line-height: 1;
  text-align: center;
}
.text {
  margin-bottom: 20px;
  color: #333;
  font-size: 30px;
  line-height: 1.2;
  font-weight: 700;
}
.author {
  background-image: linear-gradient(110deg, #ff6945 0%, #8047ca 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: 0;
  color: #121212;
  font-size: 40px;
  text-align: right;
  padding-right: 40px;
  transition: opacity 1s linear;
}

@keyframes card {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.6;
  }
}

.sun {
  display: none;
  width: 150px;
  height: 150px;
  position: absolute;
  top: -100px;
  left: 200px;
  opacity: 0;
  animation: sun 4s linear forwards;
}
@keyframes sun {
  from {
    opacity: 0;
    top: -100px;
  }
  to {
    opacity: 1;
    top: 60px;
  }
}

.face {
  display: none;
  width: 80px;
  height: 87px;
  position: absolute;
  top: 42%;
  left: 46%;
  opacity: 0;
  animation: face 2s linear forwards;
}
@keyframes face {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.me {
  display: none;
  position: absolute;
  top: 130%;
  left: 40%;
  animation: me 3s linear 1s forwards;
}
@keyframes me {
  from {
    top: 130%;
  }
  to {
    top: 80%;
  }
}

.heart {
  display: none;
  width: 60px;
  position: absolute;
  top: 68%;
  left: 80%;
  animation: heart 1s linear 4s forwards;
  opacity: 0;
}

@keyframes heart {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.btn {
  position: absolute;
  top: 30%;
  left: 30%;

  cursor: pointer;
}

.is-hidden {
  display: block;
}

.link {
  position: absolute;
  top: 90%;
  left: 5%;
  font-size: 50px;
  cursor: pointer;
  text-decoration: none;
}

/* button */
.cta {
  position: relative;
  top: 30px;
  left: 40%;
  margin: auto;
  padding: 19px 22px;
  transition: all 0.2s ease;
  background-color: transparent;
  border: none;
}
.cta::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  border-radius: 28px;
  background: #ffab9d;
  width: 56px;
  height: 56px;
  transition: all 0.3s ease;
}

.cta span {
  position: relative;
  font-size: 16px;
  line-height: 18px;
  font-weight: 900;
  letter-spacing: 0.25em;
  text-transform: uppercase;
  vertical-align: middle;
}
.cta svg {
  position: relative;
  top: 0;
  margin-left: 10px;
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke: #111;
  stroke-width: 2;
  transform: translateX(-5px);
  transition: all 0.3s ease;
}

.cta:hover.cta::before {
  width: 100%;
  background-color: transparent;
}
.cta:hover.cta svg {
  display: none;
}
.cta:hover.cta span {
  color: transparent;
}
